import React, { FunctionComponent } from 'react';
import { Router, Switch, Route } from 'react-router-dom';

import { AuthLayout } from '../../modules/layout/AuthLayout';
import { MainLayout } from '../../modules/layout/MainLayout';
import { history, RoutePath } from '../../utils/history-provider';
import { Dashboard } from '../dashboard';
import { Signin } from '../signin';
import { Start } from '../start';

import { NotFound } from './NotFound';

export interface RoutesProps {}

export const Routes: FunctionComponent<RoutesProps> = (props) => {
  return (
    <Router history={history}>
      <Switch>
        <Route
          path={RoutePath.Dashboard}
          component={WithMainLayout(Dashboard)}
        />
        <Route path={RoutePath.Start} component={WithAuthLayout(Start)} />
        <Route path={RoutePath.Signin} component={WithAuthLayout(Signin)} />
        <Route path="*" component={WithMainLayout(NotFound)} />
      </Switch>
    </Router>
  );
};

const WithMainLayout =
  (Component: FunctionComponent) =>
  ({ ...props }) => {
    return (
      <MainLayout>
        <Component {...props} />
      </MainLayout>
    );
  };

const WithAuthLayout =
  (Component: FunctionComponent) =>
  ({ ...props }) => {
    return (
      <AuthLayout>
        <Component {...props} />
      </AuthLayout>
    );
  };
